export default {
	data() {
		return {
			ListFlag:null,
			time:new Date().getTime(),
			list:[]
			
		}
	},
	onLoad(options) {
	},
	onShow(){
		this.yiQiangDan()
	},
	filters:{
		tm:function(el){
			let t= Math.round((new Date().getTime()/1000));
			let f= Math.floor((1800-(t-el))/60)
			let s= Math.floor((1800-(t-el))%60)
			return f+"分"+s+"秒"
		}
	},
	methods: {
		dianhua:function(i){
			console.log(i)
			uni.makePhoneCall({
				phoneNumber:i.tel
			})
		},
		f1:function(i){
			console.log(i)
			this.ListFlag=null;
			if(1800-((this.time/1000)-this.list[i].endTime)>0){
				let that=this;
					let usurl=that.url1+"/dinghai/order/deleteOrder";
					uni.showLoading({  mask: false,   title: '正在抢单'  });
					uni.request({
						url: usurl,
						header:{
							"Content-Type" : "application/x-www-form-urlencoded"
						},
						method:'POST',
						data: {
							worksId:that.list[i].worksId,
							id:that.list[i].id
						},
				    success: (res) => {
						clearInterval(that.list[i].getInter)
						that.list.splice(i,1);
						uni.showToast({
							title:'取消成功'
						})
				    },
					fail : function(err){
						uni.showToast({
							title:"网咯错误",
							icon:"none"
						});
					}
				});
			}else{
				uni.showToast({
					icon:"none",
					title:"已过取消时间"
				})
			}
		},
		xian:function(i){
			if(i!=this.ListFlag){
				this.ListFlag=i;
			}else{
				this.ListFlag=null
			}
		},
		back : function(){
		   uni.navigateBack({
		
		   });
		},
		yiQiangDan:function(){
			let that=this;
				let usurl=that.url1+"/dinghai/order/selectMyOrder";
				// uni.showLoading({  mask: false,   title: '正在抢单'  });
				uni.request({
					url: usurl,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
					data: {
						useruid:that.user.uid
					},
			    success: (res) => {
					that.list=res.data.flag
					for(let i=0;i<that.list.length;i++){
						if(1800-((that.time/1000)-that.list[i].endTime)>0){
							that.list[i]["getInter"]=setInterval(function(){
								that.list[i].endTime--
							},1000)
						}
					}
			    },
				fail : function(err){
					uni.showToast({
						title:"网咯错误",
						icon:"none"
					});
				}
			});
		},
		   //  点击整体时显示功能
		showgn : function(){
			// uni.showActionSheet({
									
